import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import AppBar from 'material-ui/AppBar';
import RaisedButton from 'material-ui/RaisedButton';
import TextField from 'material-ui/TextField';
import React, { Component } from "react";
import axios from 'axios';

class Login extends Component {

    constructor(props){
    super(props);
    this.state={
        username:"",
        password:"",
        ulogovaniKorisnik:{}
        }
    }

 handleRegister(){
    this.props.history.push({
        pathname: '/register',
    })
}

goToHomePage(){
    this.props.history.push({
        pathname: '/home',
        state: { userID: this.state.ulogovaniKorisnik.userID,
            username: this.state.ulogovaniKorisnik.username }
    })
}

handleLogin(){

    var headers = {
        'headers': {
            'Content-Type': 'application/json;charset=UTF-8'
        }
    }
    var data = {
        "username": this.state.username,
        "password": this.state.password
    }
    axios.post('http://localhost:8081/login', data, headers)

        .then((res) => {
            const ulogovaniKorisnik = res.data;
            this.setState({ulogovaniKorisnik}, this.goToHomePage);
            console.log("RESPONSE RECEIVED: ", res);
        })
        .catch((err) => {
            console.log("AXIOS ERROR: ", err);
        })
    
}

handleChange = event => {
    this.setState({
        [event.target.id]: event.target.value
    });
}

render() {
    return (
      <div>
        <MuiThemeProvider>
          <div>
          <AppBar
             title="Login"
           />
           <TextField
             hintText="Enter your Username"
             floatingLabelText="Username"
            //  value={this.state.username}
            //  onChange={this.handleChange}
             onChange = {(event,newValue) => this.setState({username:newValue})}
             />
           <br/>
             <TextField
               type="password"
               hintText="Enter your Password"
               floatingLabelText="Password"
               onChange = {(event,newValue) => this.setState({password:newValue})}
               />
             <br/>
             <RaisedButton label="Submit" primary={true} style={style} onClick={(event) => this.handleLogin(event)}/>
             <br/>
             {"Not registered yet, Register Now"}
             <br/>
             <RaisedButton label="Register" primary={true} style={style} onClick={(event) => this.handleRegister(event)}/>
         </div>
         </MuiThemeProvider>
      </div>
    );
  }
}
const style = {
 margin: 15,
};
export default Login;


