import React, { Component } from 'react';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import AppBar from 'material-ui/AppBar';
import RaisedButton from 'material-ui/RaisedButton';
import TextField from 'material-ui/TextField';
import axios from 'axios';
import MenuAppBar from './MenuAppBar'; 

class Register extends Component {
    
  constructor(props){
    super(props);
    this.state={
      username:'',
      password:'',
      newUser:{}
    }
  }

  handleRegister(){
    var headers = {
      'headers': {
          'Content-Type': 'application/json;charset=UTF-8'
      }
  }
  var data = {
    "username": this.state.username,
    "password": this.state.password
  }
  axios.post('http://localhost:8081/register', data, headers)

      .then((res) => {
          const newUser = res.data;
          this.setState({newUser}, this.goToHomePage);
          console.log("RESPONSE RECEIVED: ", res);
      })
      .catch((err) => {
          console.log("AXIOS ERROR: ", err);
      })
  }

  goToHomePage(){
    this.props.history.push({
        pathname: '/home',
        state: { userID: this.state.newUser.userID,
            username: this.state.newUser.username }
    })
}

  render() {
    return (
      <div>
        <MuiThemeProvider>
          <div>
          <MenuAppBar
             title="Register"
           />
           <TextField
             hintText="Enter your Username"
            //  type="email"
             floatingLabelText="Username"
             onChange = {(event,newValue) => this.setState({username:newValue})}
             />
           <br/>
           <TextField
             type = "password"
             hintText="Enter your Password"
             floatingLabelText="Password"
             onChange = {(event,newValue) => this.setState({password:newValue})}
             />
           <br/>
           <RaisedButton label="Submit" primary={true} style={style} onClick={(event) => this.handleRegister(event)}/>
          </div>
         </MuiThemeProvider>
      </div>
    );
  }
}
const style = {
  margin: 15,
};
export default Register;


